@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;600;700;900&display=swap");
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&display=swap");
.header nav {
  padding: 0em 2em;
  background: url("../../Design/Assets/png/Navigation Bar 5.jpg") no-repeat center/cover;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__menu a {
  margin-right: 3em;
  color: black;
  font-weight: 700;
  font-size: 1.7em; }
  .header__menu a:nth-last-child(1) {
    border: 1px solid black;
    padding: 0.3em 0.3em 0em 0.3em;
    border-radius: 10px; }

.header__text {
  background: url("../../Design/Assets/png/En chiffres-24.png");
  text-align: center;
  line-height: 2; }

.header__title {
  font-weight: 700;
  font-size: 1.3em; }

.header__parag {
  width: 42em;
  font-weight: 700;
  margin: 0px auto;
  font-size: 1.2em;
  color: #1078ad; }

.main {
  display: flex;
  justify-content: space-between;
  padding: 2em 3em 2em 1em;
  font-weight: 700; }
  .main__img {
    width: 48%;
    height: 42em; }
  .main__text {
    padding-top: 2em; }
    .main__text img {
      width: 7em;
      margin-left: 47%; }
    .main__text a {
      color: white;
      font-size: 3em; }
  .main__btn {
    margin-top: 1em;
    background-color: #1078ad;
    padding: 0.3em 4em;
    text-align: center;
    border-radius: 5px; }
  .main__parag {
    text-align: center;
    line-height: 1.6;
    font-size: 2.3em;
    color: #7F7F7F; }
    .main__parag--color {
      color: #1078ad; }

@media only screen and (max-width: 1745px) {
  .main__text {
    font-size: 0.8rem !important; }
    .main__text a {
      font-size: 0.8rem !important; }
    .main__text img {
      width: 5em;
      margin-left: 47%; }
  .main__img {
    height: 32em; } }

@media only screen and (max-width: 1415px) {
  .main__text {
    font-size: 0.6rem !important; }
    .main__text a {
      font-size: 0.6rem !important; }
    .main__text img {
      width: 4em;
      margin-left: 47%; }
  .main__img {
    height: 22em; } }

@media only screen and (max-width: 1080px) {
  .main {
    flex-wrap: wrap; }
    .main__text {
      font-size: 0.4rem !important; }
    .main__img {
      height: 17em; } }

@media only screen and (max-width: 790px) {
  * {
    font-size: 0.9rem !important; }
  .header__logo img {
    width: 4em; }
  .main {
    padding: 2em;
    margin-left: 1em; }
    .main__img {
      width: 100%; }
    .main__parag {
      font-size: 3em !important; }
    .main__text {
      font-size: 0.6rem !important;
      text-align: center; }
      .main__text img {
        margin-left: 0% !important; }
    .main__btn {
      padding: 1.3em 4em; }
      .main__btn a {
        font-size: 1rem !important; } }

@media only screen and (max-width: 540px) {
  * {
    font-size: 0.87rem !important; }
  .main__img {
    height: 25em; }
  .main__text {
    font-size: .5rem !important; } }

@media only screen and (max-width: 540px) and (max-width: 375px) {
  * {
    font-size: 0.8rem !important; }
  .header__parag {
    width: 30em;
    color: black;
    font-weight: 700;
    padding-bottom: 0.5em; }
  .header__text {
    line-height: 1.9 !important; }
  .header__logo img {
    width: 4.5em; }
  .header__menu a {
    font-size: 1.3em !important;
    margin-right: 1em; }
  .header__title {
    font-size: 1.4em !important;
    padding-top: 0.2em;
    color: #1078ad; }
  .main {
    padding: 0em; }
    .main__img {
      height: 28em;
      width: 96%;
      padding-top: 2em; }
    .main__parag {
      font-size: 0.9em !important;
      max-width: 22em;
      margin: 0px auto; }
      .main__parag--color {
        font-size: 1.2em !important;
        margin-left: 1.6em; }
    .main__text {
      font-size: 1.2em !important;
      line-height: 1.5;
      padding-top: 1.3em; }
      .main__text img {
        width: 4.5em;
        margin-left: 0%;
        margin: 1.8em 0em 1.2em 0em; } }

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-size: 1rem;
  text-decoration: none;
  font-family: 'Cairo', sans-serif; }
